/*
  If the number of TAB files has changed, the following variable is required.
  And it must be defined before `@import`.
*/
$tab-count: 5; // plus 1 for home tab

@import "jekyll-theme-chirpy";

/* append your custom style below */

// Video Player

@import url(/assets/css/plyr/plyr.css);
.plyr{
  border-radius: 8px;
  margin: 15px 0px;
}

@font-face {
  font-family: chiwer;
  // src: url(https://api.allorigins.win/raw?url=https://gitlab.com/Alimjoo/storage/-/raw/main/fonts/ukijchik.ttf);
  src: url(https://piyazon.top/storage/assets/fonts/ukijchik.ttf);
}
@font-face {
  font-family: alpida;
  src: url(https://piyazon.top/storage/assets/fonts/alpida%20unicode%20system.ttf);
}
@font-face {
  font-family: ekran;
  src: url(https://piyazon.top/storage/assets/fonts/ukijekran.ttf);
}
@font-face {
  font-family: kesmetuz;
  src: url(https://piyazon.top/storage/assets/fonts/ukijkesmetuz.ttf);
}
@font-face {
  font-family: ukij_tor;
  src: url(https://piyazon.top/storage/assets/fonts/ukijtor.ttf);
}


.mx-auto{
  border: 2px solid rgb(94, 148, 233) !important;
    border-radius: 50px !important;
    padding: 0px 2.5px;
  	background-color:lightgray;
   box-shadow: 0 9px 6px -6px black;
}

.linux-logo{
  border: 2px solid rgb(94, 148, 233);
  border-radius: 30px !important;
  padding: 0px 2.5px;
  background-color:lightgray;
 box-shadow: 0 8px 6px -6px black;
}

blockquote{
  border-color:dodgerblue;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

