.MathJax_Display {
  overflow-x: scroll;
  overflow-y: hidden;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.MathJax_Display::-webkit-scrollbar {
  display: none;
}



.right-to-left{
  direction: rtl;
  text-align: right;
  font-family: chiwer;
  font-weight: normal;
}

.access,
#archives,
#tags,
#page-tag,
#page-category,
.post-tags,
#access-tags,
#post-list .post-preview .post-content>p,
#search-results .post-meta,
#toc-wrapper,
#topbar-wrapper,
#search-results p
{
  font-family: Georgia,Cambria, kesmetuz, kesmetuz;
  font-weight: normal;
}

#topbar-title,
.post-navigation,
#related-posts,
.card-body h3,
#page-tag > h1,
#page-category > h1,
.categories,
#search-results a,
#post-list .post-preview h1,
.table-wrapper
{
  font-family: ekran, 'Lato', 'Microsoft Yahei', sans-serif;
  font-weight: normal;
}

#search-results p{
  text-align:left;
}
#post-list .post-preview .post-content>p,
#post-list .post-preview h1
{
  text-align: justify;
}

.mr-auto{
  direction: ltr;
}

#archives{
  letter-spacing: normal;
}

