video{
  width: 100%;
  border-radius: 10px;
  margin: 15px 0px;
}
video::cue{
  font-family: 'Lato', 'Microsoft Yahei', sans-serif, ekran;
  font-weight: normal;
}
video:focus {
  outline: none;
}
